import { Injectable } from '@angular/core';
import { WindowRef } from './window.service';
import * as io from 'socket.io-client';
import { LocalStorageService, SessionStorageService } from 'ngx-webstorage';

@Injectable({providedIn: 'root'})
export class SocketIoService {
  client: any;

  constructor(
    private $window: WindowRef,
    private localStorage: LocalStorageService,
    private sessionStorage: SessionStorageService,
  ) {
  }

  connect() {
    if (this.client) {
      return this.client;
    }

    const token = this.localStorage.retrieve('authenticationToken') || this.sessionStorage.retrieve('authenticationToken');
    const loc = this.$window.nativeWindow.location;
    let url;
    url = loc.host;
    // url = 'http://sample.aztrace.vn';
    const socket = io(`${url}`, {
      path: '/socket.io/',
      query: {token: token},
      transports: ['websocket'],
    });
    // const socket = io(`${SOCKET_IO_HOST}:${SOCKET_IO_PORT}`);
    console.log('Begin request data.');
    socket.on('connect', function() {
      console.log('Client socket io connected');
    });
    socket.on('disconnect', function() {
      console.log('Client socket io disconnected');
    });
    socket.on('end error', function (msg) {
      console.log(msg);
      console.log('Client socket connection failed');
    });

    this.client = socket;
    return socket;
  }

  disconnect() {
    this.client.disconnect();
    this.client = null;
  }

}
