import { JhiAlertService, JhiEventManager } from 'ng-jhipster';
import { HttpInterceptor, HttpRequest, HttpResponse, HttpHandler, HttpEvent } from '@angular/common/http';
import { Injector } from '@angular/core';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

export class NotificationInterceptor implements HttpInterceptor {
  private alertService: JhiAlertService;
  private eventManager: JhiEventManager;

  // tslint:disable-next-line: no-unused-variable
  constructor(private injector: Injector) {
    setTimeout(() => {
      this.alertService = injector.get(JhiAlertService);
      this.eventManager = injector.get(JhiEventManager);
    });
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const exceptAlert = request.params.get('exceptAlert');
    return next.handle(request).pipe(
      tap(
        (event: HttpEvent<any>) => {
          if (event instanceof HttpResponse && !exceptAlert) {
            const arr = event.headers.keys();
            let alert = null;
            let params = null;
            arr.forEach(entry => {
              if (entry.toLowerCase().endsWith('app-alert')) {
                alert = event.headers.get(entry);
                alert = decodeURIComponent(escape(alert));
              }
              if (entry.toLowerCase().endsWith('app-params')) {
                params = event.headers.get(entry);
              }
            });
            if (alert) {
              if (typeof alert === 'string') {
                if (this.alertService) {
                  this.alertService.success(alert, params, null);
                }
                if (this.eventManager !== undefined) {
                  this.eventManager.broadcast({
                    name: 'app.app-notifier',
                    content: {message: alert, type: 'success', params}
                  });
                }
              }
            }
          }
        },
        (err: any) => {}
      )
    );
  }
}
