import { PadNumberPipe } from '../pipes/pad-number.pipe';
import { isMoment } from 'moment';

export function parseNgbDateToLocalDateString(ngbDate: any): any {
  const paNumberPipe = new PadNumberPipe();
  const month = paNumberPipe.transform(ngbDate.month, 2);
  const day = paNumberPipe.transform(ngbDate.day, 2);
  return `${ngbDate.year}-${month}-${day}`;
}

export function getDateOfISOWeek(w, y) {
  const simple = new Date(y, 0, 1 + (w - 1) * 7);
  const dow = simple.getDay();
  const isoWeekStart = simple;
  if (dow <= 4) {
    isoWeekStart.setDate(simple.getDate() - simple.getDay() + 1);
  } else {
    isoWeekStart.setDate(simple.getDate() + 8 - simple.getDay());
  }
  return isoWeekStart;
}

export function formatDate(date) {
  const d = new Date(date);
  let month = '' + (d.getUTCMonth() + 1);
  let day = '' + d.getUTCDate();
  const year = d.getUTCFullYear();

  if (month.length < 2) { month = '0' + month; }
  if (day.length < 2) { day = '0' + day; }

  return [year, month, day].join('-');
}

export function getMonday(date) {
  const day = date.getUTCDay() || 7;
  if (day !== 1) {
    date.setUTCHours(-24 * (day - 1));
  }
  return date;
}

export function getWeek(date: Date) {
  const onejan = new Date(date.getUTCFullYear(), 0, 1);
  return Math.ceil((((+date - +onejan) / 86400000) + onejan.getUTCDay() + 1) / 7);
}

export function getWeekNumber(d) {
  // Copy date so don't modify original
  d = new Date(+d);
  d.setHours(0, 0, 0);
  // Set to nearest Thursday: current date + 4 - current day number
  // Make Sunday's day number 7
  d.setDate(d.getDate() + 4 - (d.getDay() || 7));
  // Get first day of year
  const yearStart: any = new Date(d.getFullYear(), 0, 1);
  // Calculate full weeks to nearest Thursday
  const weekNo = Math.ceil(( ( (d - yearStart) / 86400000) + 1) / 7);
  // Return array of year and week number
  return [d.getFullYear(), weekNo];
}

export function weeksInYear(year) {
  const month = 11;
  let day = 31;
  let week;

  // Find week that 31 Dec is in. If is first week, reduce date until
  // get previous week.
  do {
    const d = new Date(year, month, day--);
    week = getWeekNumber(d)[1];
  } while (week === 1);

  return week;
}

/**
 * Parse string to Date
 * @param value value
 * @return Date time
 */
function stringToTime(value: string): Date {
  return new Date(value);
}

/**
 * Check input date in specify week
 * @param cur current
 * @param week week
 * @param year year
 */
export function checkDateInWeek(cur: any, week: number, year: number) {
  if (!cur) {
    return false;
  }
  if (typeof cur === 'string') {
    cur = stringToTime(cur);
  }
  if (cur instanceof Date) {
    const result = getWeekNumber(cur);
    const resultYear = cur.getFullYear();
    return result[1] === week && resultYear === year;
  } else if (isMoment(cur)) {
    return cur.isoWeek() === week && cur.isoWeekYear() === year;
  } else {
    return false;
  }
}
