@Injectable({ providedIn: 'root' })
export class PermissionService {
  public permissions = [];
  constructor(private accountService: AccountService, private localStorage: LocalStorageService) {
  }

  async store(roleId) {
    const permissionsStore = this.localStorage.retrieve('rules');
    if (permissionsStore) {
      this.permissions = permissionsStore;
      return this.permissions;
    } else {
      return this.accountService
        .authorization(roleId)
        .toPromise()
        .then(response => {
          const result = response.body;
          this.permissions = [];
          this.localStorage.clear('rules');
          if (result) {
            this.permissions = this.convertRulesToPermissions(result);
            this.localStorage.store('rules', this.permissions);
          }
          return this.permissions;
        })
        .catch(() => {
          return [];
        });
    }
  }

  /**
   * Convert rules to UI permissions
   *
   * @private
   * @param {IRule[]} rules
   * @returns {string[]}
   * @memberof PermissionService
   */
  private convertRulesToPermissions(rules: IRule[]): string[] {
    const validRules: IRule[] = rules
    .filter(item => item.v4 === '*' || item.v4 === String(true) || item.v4 === String(true).toUpperCase());
    return ruleToPermission
    .filter(item => {
      const validRule = validRules.find(rule => {
        const rulePath = rule.v1.replace(/\//g, '').toLowerCase();
        if (rule.v5 === item.rule.v5) {
          return true;
        }
        if (isArray(item.rule.v5) && item.rule.v5.includes(rule.v5 as string)) {
          return true;
        }
        if (rule.v2 === '*' && (rulePath === item.rule.v5 || rulePath === `${item.rule.v5}*`)) {
          return true;
        }
        return false;
      });
      return !!validRule;
    }).map(item => item.per);
  }
}
