import { Locale, Locales } from './enums';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { Injectable } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
@Injectable()
export class LanguageService {
  private _change = new BehaviorSubject<any>({});
  onChange = this._change.asObservable();
  private _select = new BehaviorSubject<any>({});
  onSelect = this._select.asObservable();
  storageKey = 'language';
  private _defaultLang = Locale.en_US;
  constructor(
    public translate: TranslateService,
  ) { }

  build() {
    this.translate.addLangs(Locales.map((locale) => {
      return locale.key;
    }));
    const storedLang: string|any = localStorage.getItem(this.storageKey);
    let defaultLang = this._defaultLang;
    if (!storedLang && storedLang !== null && storedLang !== '') {
      defaultLang = storedLang;
    }
    this.translate.use(defaultLang);
    this.translate.setDefaultLang(defaultLang);
    this.select(defaultLang);
    this._change.next(defaultLang);
    return defaultLang;
  }

  use(): Observable<any> {
    const lang: string = localStorage.getItem(this.storageKey);
    if (lang !== '') {
      return this.translate.use(lang);
    }
    return EMPTY;
  }

  select(locale: string) {
    const storedLang: string = localStorage.getItem(this.storageKey);
    if (storedLang !== locale) {
      localStorage.setItem(this.storageKey, locale);
    }

    return this._select.next({locale: locale});
  }

  getCurrentLocale() {
    return this.translate.currentLang;
  }

  get(key: string, params = {}): Observable<string | any> {
    return this.translate.get(key, params);
  }

  reload(): Observable<any> {
    const lang: string = localStorage.getItem(this.storageKey);
    if (lang !== '') {
      return this.translate.reloadLang(lang);
    }
    return EMPTY;
  }

  /**
   * Change locale
   * @param key
   */
  async change(key: string): Promise<any> {
    this.select(key);
    const resultReloaded = await this.reload().toPromise();
    const resultUsed = await this.use().toPromise();
    return [resultReloaded, resultUsed];
  }
}
