import { JhiEventManager } from 'ng-jhipster';
import { FcmService } from '../../core/fire/fcm.service';
import { LoginService } from './../../core/auth/login.service';
import {AdminLayoutService} from './../admin-layout/admin-layout.service';
import {Component, OnInit} from '@angular/core';
import {BreakpointObserver} from '@angular/cdk/layout';
import {AuthServerProvider, IAccount, Principal} from '../../core';
import {LocalStorageService} from 'ngx-webstorage';
import {Router} from '@angular/router';
import { AngularFireMessaging } from '@angular/fire/messaging';
import { NotifierService } from 'angular-notifier';
import { IAngularFireMessage } from '../../core/fire/fcm';

@Component({
  selector: 'app-topbar',
  templateUrl: './topbar.component.html',
  styleUrls: ['./topbar.component.scss']
})
export class TopbarComponent implements OnInit {
  account: IAccount;
  // constructor(public adminLayoutService: AdminLayoutService) { }
  avatar = 'assets/images/img-user.png';
  firstName = '';

  constructor(
    public principal: Principal,
    public adminLayoutService: AdminLayoutService,
    private $localStorage: LocalStorageService,
    private afMessaging: AngularFireMessaging,
    private loginService: LoginService,
    private notifierService: NotifierService,
    private fcmService: FcmService,
    private eventManager: JhiEventManager,
  ) {
    this.account = {} as any;
    const fcmToken = this.fcmService.getToken();
    if (fcmToken) {
      this.listen();
    }
  }

  getIdentity() {
    this.principal.getAuthenticationState().subscribe((account) => {
      if (account !== undefined && account !== null) {
        this.account = account;
        if (account.imageUrl !== null && account.imageUrl !== '' && account.imageUrl !== undefined) {
          this.avatar = account.imageUrl;
        } else if (account.avatar !== null && account.avatar !== '' && account.avatar !== undefined) {
          this.avatar = account.imageUrl;
        } else {
          this.avatar = 'assets/images/img-user.png';
        }
        this.firstName = account.firstName;
      }
    });
    this.updateUI();
  }

  ngOnInit() {
    this.getIdentity();
  }

  logout() {
    this.loginService.logout();
  }

  updateUI() {
    const userDetail = this.$localStorage.retrieve('userDetail');
    if (userDetail) {
      if (userDetail.imageUrl !== null && userDetail.imageUrl !== '' && userDetail.imageUrl !== undefined) {
        this.avatar = userDetail.imageUrl;
      } else if (userDetail.avatar !== null && userDetail.avatar !== '' && userDetail.avatar !== undefined) {
        this.avatar = userDetail.imageUrl;
      } else {
        this.avatar = 'assets/images/img-user.png';
      }
      this.firstName = userDetail.firstName;
    }
  }

  listen() {
    this.afMessaging.messages
      .subscribe((message: IAngularFireMessage) => {
        // console.log(message);
        if (message && message.notification) {
          this.eventManager.broadcast({
            name: 'app.app-notifier',
            content: {
              message: message.notification.title,
              body: message.notification.body,
              type: 'error'
            },
          });
        }
      });
  }

  search() {
    // this.eventManager.broadcast({
    //   name: 'app.app-notifier',
    //   content: {
    //     message: `Cập nhật thông số môi trường`,
    //     body: '<p>You can use the mark tag to <mark>highlight</mark> text.</p> <p><del>This line ' +
    //     'of text is meant to be treated as deleted text.</del></p> <p><s>This line of text is meant to be treated as' +
    //     'no longer accurate.</s></p><p><ins>This line of text is meant to be treated as an addition to the document.</ins>' +
    //     '</p> <p><u>This line of text will render as underlined</u></p> <p><small>This line of text is meant to be' +
    //     'treated as fine print.</small></p> <p><strong>This line rendered as bold text.</strong></p> <p><em>This line' +
    //     'rendered as italicized text.</em></p>',
    //     type: 'error',
    //   },
    //   config: {
    //     behaviour: {
    //       autoHide: false,
    //     },
    //   } as NotifierConfig,
    // });
  }
}
