import { NgModule } from '@angular/core';
import { SharedLibsModule } from './shared-libs.module';
import {
  AlertNotifierComponent,
  LoadingScreenComponent,
  LanguageComponent,
} from './components';
import {
  DateFormatPipe,
  NumberFormatPipe,
} from './pipes';
import { HasAnyAuthorityDirective } from './directives/auth/has-any-authority.directive';
import { HasAnyAuthorityDisablementDirective } from './directives/auth/has-any-authority-disablement.directive';
import { RouteService } from './services/route.service';
import { LanguageService } from './components/language/language.service';
import { SideNavDirective } from './directives/side-nav.directive';

@NgModule({
  imports: [SharedLibsModule],
  declarations: [
    AlertNotifierComponent,
    LoadingScreenComponent,
    LanguageComponent,
    DateFormatPipe,
    NumberFormatPipe,
    HasAnyAuthorityDirective,
    HasAnyAuthorityDisablementDirective,
    SideNavDirective,
  ],
  providers: [
    LanguageService,
    RouteService,
  ],
  exports: [
    SharedLibsModule,
    AlertNotifierComponent,
    LoadingScreenComponent,
    LanguageComponent,
    DateFormatPipe,
    NumberFormatPipe,
    HasAnyAuthorityDirective,
    HasAnyAuthorityDisablementDirective,
    SideNavDirective,
  ],
})
export class SharedCommonModule {}
