import { Directive, Input, TemplateRef, ViewContainerRef } from '@angular/core';
import { Principal } from '../../../core/auth/principal.service';
import { PermissionService } from '../../../core/account/permisions.service';
import { LocalStorageService } from 'ngx-webstorage';
import { environment } from '../../../../environments/environment';

/**
 * @whatItDoes Conditionally includes an HTML element if current user has any
 * of the authorities passed as the `expression`.
 *
 * @howToUse
 * ```
 *   <some-element *appHasAnyAuthority="'scope_name'">...</some-element>
 *
 *   <some-element *appHasAnyAuthority="['scope_name', 'scope_name']">...</some-element>
 * ```
 */
@Directive({
  selector: '[appHasAnyAuthority]'
})
export class HasAnyAuthorityDirective {
  private authorities: string[];

  constructor(private permissionService: PermissionService,
              private localStorage: LocalStorageService,
              private principal: Principal,
              private templateRef: TemplateRef<any>,
              private viewContainerRef: ViewContainerRef) {}

  @Input()
  set appHasAnyAuthority(value: string | string[]) {
    this.authorities = typeof value === 'string' ? [value] : value;
    this.updateView();
    // Get notified each time authentication state changes.
    this.principal.getAuthenticationState().subscribe(identity => this.updateView());
  }

  private updateView(): void {
    if (environment.design) {
      this.viewContainerRef.clear();
      this.viewContainerRef.createEmbeddedView(this.templateRef);
      return;
    }
    const userDetail: any = this.localStorage.retrieve('userDetail');
    if (userDetail && userDetail.roleId) {
      this.principal.hasAnyAuthority(this.authorities).then(result => {
        this.viewContainerRef.clear();
        if (result) {
          this.viewContainerRef.createEmbeddedView(this.templateRef);
        }
      });
    } else {
      this.viewContainerRef.clear();
      // this.viewContainerRef.createEmbeddedView(this.templateRef);
    }
  }
}
