import { LoginService } from './login.service';
import { JhiEventManager } from 'ng-jhipster';
import { Injectable, isDevMode } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';

import { Principal } from '../auth/principal.service';
import { StateStorageService } from './state-storage.service';
import { IAccount } from '../account/account.model';
import { LocalStorageService } from 'ngx-webstorage';
import { environment } from '../../../environments/environment';

@Injectable({ providedIn: 'root' })
export class UserRouteAccessService implements CanActivate {
  constructor(
    private router: Router,
    private principal: Principal,
    private stateStorageService: StateStorageService,
    private localStorage: LocalStorageService,
    private eventManager: JhiEventManager,
    private loginService: LoginService,
  ) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | Promise<boolean> {
    const authorities = route.data['authorities'];
    if (environment.design) {
      return true;
    }

    // We need to call the checkLogin / and so the principal.identity() function, to ensure,
    // that the client has a principal too, if they already logged in by the server.
    // This could happen on a page refresh.
    return this.checkLogin(authorities, state.url);
  }

  /**
   * Return url priority url if dont have dashboard scope
   */
  private priorityRedirect() {
    const rules: string[] = this.localStorage.retrieve('rules');
    if (rules && rules.includes('can_change_profile')) {
      return '/personal-setting';
    } else if (!rules.length) {
      return '/dashboard';
    }
    return null;
  }

  alertForbidden() {
    this.eventManager.broadcast({
      name: 'app.app-notifier',
      content: {message: 'login.messages.error.forbidden', type: 'error'}
    });
  }

  checkLogin(authorities: string[], url: string): Promise<boolean> {
    const principal = this.principal;
    return Promise.resolve(
      principal.identity().then((account: IAccount) => {
        const rules: string[] = this.localStorage.retrieve('rules');
        if (Array.isArray(rules) && !rules.length) {
          this.alertForbidden();
          this.loginService.logout();
          return false;
        }
        if (account) {
          if (!authorities || authorities.length === 0) {
            return true;
          }

          return principal.hasAnyAuthority(authorities).then(response => {
            if (response) {
              return true;
            }

            // if (authorities.includes('can_read_dashboard_summary')) {
            //   const link = this.priorityRedirect();
            //   if (link) {
            //     this.router.navigate([link]);
            //   }
            //   return true;
            // }

            if (isDevMode()) {
              console.error('User has not any of required authorities: ', authorities);
            }

            this.alertForbidden();

            return false;
          });
        }

        this.stateStorageService.storeUrl(url);
        this.router.navigate(['accessdenied']).then(() => {
          // only show the login dialog, if the user hasn't logged in yet
          if (!account) {
            this.router.navigate(['login']);
          }
        });
        return false;
      })
    );
  }
}
