import { Component, EventEmitter, forwardRef, Input, OnInit, Output } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { SelectFilterCriteria } from './select-filter.criteria';
import { FilterOperator } from '../../models';

const noop = () => {
};

@Component({
  selector: 'app-select',
  templateUrl: './select.component.html',
  styleUrls: ['./select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectComponent),
      multi: true,
    },
  ],
})
export class SelectComponent implements ControlValueAccessor, OnInit {
  // The internal data model
  private innerValue: any = '';

  // Placeholders for the callbacks which are later provided
  // by the Control Value Accessor
  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;

  @Input() items;
  @Input() criteria = new SelectFilterCriteria();
  @Input() name: string;
  @Input() required: string;
  @Input() label: (item: any) => void;
  @Input() placeholder: string;
  @Input() dropdownPosition = 'auto';
  @Output() ngOnFilter: EventEmitter<any> = new EventEmitter();
  @Output() ngOnChange: EventEmitter<any> = new EventEmitter();

  constructor() {
  }

  ngOnInit() {}

  // Get accessor
  get value(): any {
    return this.innerValue;
  }

  // Set accessor including call the onchange callback
  set value(v: any) {
    if (v !== this.innerValue) {
      this.innerValue = v;
      this.onChangeCallback(v);
    }
  }

  // Set touched on blur
  onBlur() {
    this.onTouchedCallback();
  }

  // Set changed on change
  onChange(event) {
    this.onChangeCallback(event);
    this.ngOnChange.emit(this.innerValue);
  }

  // From ControlValueAccessor interface
  registerOnChange(fn: any): void {
    this.onChangeCallback = fn;
  }

  // From ControlValueAccessor interface
  registerOnTouched(fn: any): void {
    this.onTouchedCallback = fn;
  }

  writeValue(value: any): void {
    if (value !== this.innerValue) {
      this.innerValue = value;
    }
  }

  // Render template option label
  renderLabel(item: any) {
    if (this.label) {
      return this.label(item);
    }
    return `${item.id}`;
  }

  /**
   * Scroll down trigger
   * Add more buffer size & callback
   * Change loading process option
   */
  ngOnFetchMore() {
    this.criteria.loading = true;
    this.criteria.moreSize();
    this.setFilter();
    setTimeout(() => {
      this.criteria.loading = false;
    }, 200);
  }

  /**
   * Search trigger
   * Reset size to default & callback
   * @param term search term
   */
  ngOnSearch(event: {item: any[], term: string}|string) {
    const term = typeof event === 'string' ? event : event.term;
    if (this.criteria.key.filterValue !== term) {
      this.criteria.key.filterValue = term;
      this.criteria.resetSize();
      this.setFilter();
    }
  }

  /**
   * Clear select value & callback
   */
  ngOnClear() {
    this.criteria.id.filterValue = null;
    this.setFilter();
  }

  /**
   * Filter callback
   */
  setFilter() {
    const req = this.criteria.getFilterParams();
    req[`key.${FilterOperator.contains}`] = this.criteria.key.filterValue;
    req.size = this.criteria.size;
    this.ngOnFilter.emit({req});
  }
}
