import { OverlayModule } from '@angular/cdk/overlay';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NotifierModule } from 'angular-notifier';
import { NgJhipsterModule } from 'ng-jhipster';
import { CookieModule } from 'ngx-cookie';

import { NOTIFIER_OPTIONS } from './constants/notifier.constant';
import { MaterialModule } from './material.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { RouterModule } from '@angular/router';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { LayoutModule } from '@angular/cdk/layout';
import { TranslateLoader, TranslateModule, TranslateModuleConfig, TranslateService } from '@ngx-translate/core';
import { CachedHttpTranslationLoader } from './components/language/language.loader';
import { environment } from '../../environments';
import { NgxWebstorageModule } from 'ngx-webstorage';
import { NgSelectModule } from '@ng-select/ng-select';
import { LanguageService } from './components/language/language.service';

export function createTranslateLoader(http: HttpClient) {
  return new CachedHttpTranslationLoader(http);
}

@NgModule({
  imports: [
    FormsModule,
    CommonModule,
    NgJhipsterModule.forRoot({
      // set below to true to make alerts look like toast
      alertAsToast: false,
      alertTimeout: 5000
    }),
    CookieModule.forRoot(),
    NotifierModule.withConfig(NOTIFIER_OPTIONS),
    ReactiveFormsModule,
    MaterialModule,
    OverlayModule,
    FlexLayoutModule,
    RouterModule,
    HttpClientModule,
    LayoutModule,
    TranslateModule.forChild({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      },
      extend: true,
    } as TranslateModuleConfig),
    NgxWebstorageModule.forRoot({ prefix: environment.appPrefix, separator: '-' }),
    NgSelectModule,
  ],
  providers: [
    TranslateService,
  ],
  exports: [
    FormsModule,
    CommonModule,
    NgJhipsterModule,
    NotifierModule,
    ReactiveFormsModule,
    MaterialModule,
    OverlayModule,
    FlexLayoutModule,
    RouterModule,
    HttpClientModule,
    LayoutModule,
    TranslateModule,
    NgxWebstorageModule,
    NgSelectModule,
  ],
})
export class SharedLibsModule {}
