import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpResponse
} from '@angular/common/http';
import { Injector } from '@angular/core';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';
import { LoadingScreenService } from '../../shared/components/loading-screen/loading-screen.service';

export class LoadingScreenInterceptor implements HttpInterceptor {

  private loadingScreenService: LoadingScreenService;
  constructor(private injector: Injector) {
    this.loadingScreenService = injector.get(LoadingScreenService);
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (environment.design && request.url.indexOf('api') > -1) {
      return next.handle(request);
    }

    const exceptLoading = request.params.get('exceptLoading');
    if (!exceptLoading) {
      this.showLoader();
    }

    return next.handle(request).pipe(tap((event: HttpEvent<any>) => {
        if (event instanceof HttpResponse && !exceptLoading) {
          this.onEnd();
        }
      },
      (err: any) => {
        if (!exceptLoading) {
        this.onEnd();
        }
      }));
  }

  private onEnd(): void {
    this.hideLoader();
  }
  private showLoader(): void {
    this.loadingScreenService.startLoading();
  }
  private hideLoader(): void {
    this.loadingScreenService.stopLoading();
  }
}
