import { Locales } from './enums';
import { Component, OnInit } from '@angular/core';
import { LanguageService } from './language.service';
import { RouteService } from '../../services/route.service';
import { Title } from '@angular/platform-browser';
import { environment as env } from '../../../../environments/environment';

@Component({
  selector: 'app-language',
  templateUrl: './language.component.html',
  styleUrls: ['./language.component.scss']
})
export class LanguageComponent implements OnInit {
  get Locales() { return Locales; }
  selectedLocale: string;
  constructor(
    private languageService: LanguageService,
    private routeService: RouteService,
    private titleService: Title,
  ) { }

  ngOnInit() {
   this.getSelectedLocale();
  }

  getSelectedLocale() {
    const localeKey = this.languageService.getCurrentLocale();
    const locale = Locales.find(i => i.key === localeKey);
    this.selectedLocale = locale ? locale.shortValue : '';
    const pageTitle = this.routeService.getFinalChild().snapshot.data.pageTitle;
    if (pageTitle) {
      this.languageService.get(pageTitle).subscribe(res => {
        this.titleService.setTitle(`${env.appTitle} - ${res || pageTitle}`);
      });
    }
  }

  onLocaleChange(e?: any, locale?: {key: string, value: string, shortValue: string}) {
    console.log(locale);
    if (e && e.currentTarget && e.currentTarget.value) {
      this.languageService.change(e.currentTarget.value).then(() => {
        this.getSelectedLocale();
      });
    } else if (locale) {
      this.languageService.change(locale.key).then(() => {
        this.getSelectedLocale();
      });
    }
  }
}
