import { DOCUMENT } from '@angular/common';
import { Directive, ElementRef, Inject } from '@angular/core';

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: '[sideNav]',
  exportAs: 'sideNav',
})
export class SideNavDirective {
  public isVisible = false;
  private _idBackdrop = 'sidebar-backdrop';
  constructor(
    private e: ElementRef,
    @Inject(DOCUMENT) private document: Document
  ) {
  }

  private toggleBackdrop(hide?: boolean) {
    let backdrop = this.document.getElementById(this._idBackdrop);
    if (backdrop && hide) {
      backdrop.style.display = 'none';
      return;
    }
    if (backdrop) {
      backdrop.style.display = this.isVisible ? 'block' : 'none';
    } else {
      backdrop =  this.document.createElement('div');
      backdrop.id = this._idBackdrop;
      backdrop.className = 'sidebar-backdrop';
      backdrop.style.display = this.isVisible ? 'block' : 'none';
      backdrop.addEventListener('click', this.toggle.bind(this));
      this.e.nativeElement.before(backdrop);
    }
  }

  toggle() {
    if (typeof this.e.nativeElement === 'object') {
      this.isVisible = this.e.nativeElement.style.display !== 'none';
      this.isVisible = !this.isVisible;
      this.e.nativeElement.style.display = this.isVisible ? 'flex' : 'none';
      if (this.e.nativeElement.classList.contains('sidebar-sm')) {
        this.toggleBackdrop();
      } else {
        this.toggleBackdrop(true);
      }
    }
  }
}
