export interface IMatListItem {
    iconName?: string;
    displayName: string;
    disabled?: boolean;
    iconClasses?: string;
    route?: string;
    children?: IMatListItem[];
    click?: any;
    key?: string;
    permission?: string | string[];
    divider?: boolean | any;
    collapsed?: boolean;
}

export class MatListItem implements IMatListItem {
    displayName: string;
    disabled?: boolean;
    iconClasses?: string;
    route?: string;
    children?: IMatListItem[];
    click?: any;
    key?: string;
    permission?: string | string[];
    divider?: any;
    collapsed?: boolean;
    iconName: string;
    constructor(props?: IMatListItem) {
      this.displayName = props.displayName ? props.displayName : null;
      this.disabled = props.disabled ? props.disabled : null;
      this.iconClasses = props.iconClasses ? props.iconClasses : null;
      this.iconName = props.iconName ? props.iconName : '';
      this.route = props.route ? props.route : null;
      this.children = props.children ? props.children.map(i => new MatListItem(i)) : [];
      this.click = props.click ? props.click : null;
      this.key = props.key ? props.key : null;
      this.permission = props.permission ? props.permission : null;
      this.divider = props.divider ? props.divider : null;
      this.collapsed = props.collapsed ? props.collapsed : false;
    }

    hasChildren() {
        return this.children && this.children.length > 0;
    }

    toggleChildren(event: any) {
        if (this.hasChildren()) {
            this.collapsed = !this.collapsed;
        }
    }
}
