import { Moment } from 'moment';
import { environment } from 'src/environments';

export interface IAccount {
  id?: any;
  login?: string;
  code?: string;
  firstName?: string;
  lastName?: string;
  fullName?: string;
  address?: string;
  birthday?: Moment;
  telephone?: string;
  gender?: number;
  email?: string;
  activated?: boolean;
  langKey?: string;
  role?: string;
  scope?: string;
  authorities?: any[];
  createdBy?: string;
  createdDate?: Date;
  lastModifiedBy?: string;
  lastModifiedDate?: Date;
  password?: string;
  userDetailId?: string;
  imageUrl?: string;
  avatar?: string;
  roleId?: string;
  customerId?: number;
}

export class Account implements IAccount {
  constructor(
    public id?: any,
    public login?: string,
    public code?: string,
    public firstName?: string,
    public lastName?: string,
    public fullName?: string,
    public address?: string,
    public birthday?: Moment,
    public telephone?: string,
    public email?: string,
    public activated?: boolean,
    public langKey?: string,
    public role?: string,
    public scope?: string,
    public authorities?: any[],
    public createdBy?: string,
    public createdDate?: Date,
    public lastModifiedBy?: string,
    public lastModifiedDate?: Date,
    public password?: string,
    public userDetailId?: string,
    public imageUrl?: string,
    public gender?: number,
    public avatar?: string,
    public roleId?: string,
    public customerId?: number,
  ) {
    this.id = id ? id : null;
    this.login = login ? login : null;
    this.code = code ? code : null;
    this.firstName = firstName ? firstName : null;
    this.lastName = lastName ? lastName : null;
    this.fullName = fullName ? fullName : null;
    this.address = address ? address : null;
    this.birthday = birthday ? birthday : null;
    this.telephone = telephone ? telephone : null;
    this.email = email ? email : null;
    this.activated = activated ? activated : false;
    this.langKey = langKey ? langKey : null;
    this.authorities = authorities ? authorities : null;
    this.createdBy = createdBy ? createdBy : null;
    this.createdDate = createdDate ? createdDate : null;
    this.lastModifiedBy = lastModifiedBy ? lastModifiedBy : null;
    this.lastModifiedDate = lastModifiedDate ? lastModifiedDate : null;
    this.password = password ? password : null;
    this.userDetailId = userDetailId ? userDetailId : null;
    this.imageUrl = imageUrl ? imageUrl : null;
    this.gender = gender ? gender : 0;
    this.avatar = avatar ? avatar : null;
    this.roleId = roleId ? roleId : null;
    this.customerId = customerId ? customerId : null;

    if (environment.design) {
      this.firstName = 'Scott';
      this.lastName = 'McTominay';
    }
  }
}
