import { Injectable } from '@angular/core';
import { Title } from '@angular/platform-browser';
import {
  ActivatedRoute, NavigationCancel, NavigationEnd, NavigationError, NavigationStart,
  Router, RouterEvent
} from '@angular/router';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { LoadingScreenService } from '../components/loading-screen/loading-screen.service';
import { LanguageService } from '../components/language/language.service';
import { environment as env } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class RouteService {
  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private loadingScreenService: LoadingScreenService,
    private titleService: Title,
    private languageService: LanguageService,
  ) { }

  /**
   * Catch navigation end to smallest child route.
   */
  getFinalChildOnNavigationEnd(): Observable<ActivatedRoute> {
    return this.router.events
    .pipe(
      filter(event => event instanceof NavigationEnd),
      map(() => this.activatedRoute.firstChild), // LayoutRoute
      map((route) => route.firstChild ? route.firstChild : route), // MidLayoutRoute
      map((route) => route.firstChild ? route.firstChild : route), // mid layout lever 2
      map((route) => route.firstChild ? route.firstChild : route), // Component
    );
  }

  getFinalChild(route?: ActivatedRoute): ActivatedRoute {
    const child = !route ? this.activatedRoute : route.firstChild;
    if (child) {
      return this.getFinalChild(child);
    } else {
      return route;
    }
  }

  // Shows and hides the loading spinner during RouterEvent changes
  private navigationInterceptor(event: RouterEvent): void {
    if (event instanceof NavigationStart) {
      this.loadingScreenService.startLoading();
    }
    if (event instanceof NavigationEnd) {
      this.loadingScreenService.stopLoading();
    }

    // Set loading state to false in both of the below events to hide the spinner in case a request fails
    if (event instanceof NavigationCancel) {
      this.loadingScreenService.startLoading();
    }
    if (event instanceof NavigationError) {
      this.loadingScreenService.stopLoading();
    }
  }

  ngOnRouterEvent() {
    this.router.events.subscribe((e: RouterEvent) => {
      this.navigationInterceptor(e);
    });
  }

  setRouteTitleOnNavEnd() {
    this.getFinalChildOnNavigationEnd().subscribe(latestRoute => {
      const pageTitle = latestRoute.snapshot.data.pageTitle;
      if (latestRoute && pageTitle) {
        this.languageService.get(pageTitle).subscribe(res => {
          this.titleService.setTitle(`${env.appTitle} - ${res || pageTitle}`);
        });
      }
    });
  }
}
