export interface INotification {
    id?: number;
    sendFrom?: number;
    shrimpPondId?: number;
    shrimpLitterId?: number;
    sendTo?: number;
    channel?: string;
    subject?: string;
    message?: string;
    status?: string;
    createdDate?: any;
    sendDate?: any;
    isRead?: number;
    isTestMessage?: number;
    type?: string;
    externalId?: number;
}

export class Notification implements INotification {
    constructor(
        public id?: number,
        public sendFrom?: number,
        public shrimpPondId?: number,
        public shrimpLitterId?: number,
        public sendTo?: number,
        public channel?: string,
        public subject?: string,
        public message?: string,
        public status?: string,
        public createdDate?: any,
        public sendDate?: any,
        public isRead?: number,
        public isTestMessage?: number,
        public type?: string,
        public externalId?: number,
    ) {
        this.id = id ? id : null;
        this.createdDate = createdDate ? createdDate : null;
        this.sendFrom = sendFrom ? sendFrom : null;
        this.shrimpPondId = shrimpPondId ? shrimpPondId : null;
        this.shrimpLitterId = shrimpLitterId ? shrimpLitterId : null;
        this.sendTo = sendTo ? sendTo : null;
        this.channel = channel ? channel : null;
        this.subject = subject ? subject : null;
        this.message = message ? message : null;
        this.status = status ? status : null;
        this.sendDate = sendDate ? sendDate : null;
        this.isRead = isRead ? isRead : null;
        this.isTestMessage = isTestMessage ? isTestMessage : null;
        this.type = type ? type : null;
        this.externalId = externalId ? externalId : null;
    }
}

export interface INotificationCollection {
    notificationDTOs?: INotification[];
    numberRead?: number;
    numberUnread?: number;
    numberReadTotal?: number;
    numberUnreadTotal?: number;
}

export class NotificationCollection implements INotificationCollection {
    constructor(
        public notificationDTOs?: INotification[],
        public numberRead?: number,
        public numberUnread?: number,
        public numberReadTotal?: number,
        public numberUnreadTotal?: number,
    ) {
        this.notificationDTOs = notificationDTOs ? notificationDTOs : [];
        this.numberRead = numberRead ? numberRead : 0;
        this.numberUnread = numberUnread ? numberUnread : 0;
        this.numberReadTotal = numberReadTotal ? numberReadTotal : 0;
        this.numberUnreadTotal = numberUnreadTotal ? numberUnreadTotal : 0;
    }
}
