import { IRule } from './../../entities/rule/rule.model';
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

import {DATE_TIME_FORMAT} from '../../shared/constants/input.constants';
import {IAccount} from './account.model';
import {catchError, map} from 'rxjs/operators';
import * as moment from 'moment';
import { environment } from '../../../environments';
import { IPasswordChange } from '../../shared/models/password-change';

// const baseUrlApi = "http://localhost:8080/";
type EntityAuthorityResponseType = IRule[];
type EntityType = IAccount;
type EntityResponseType = HttpResponse<IAccount>;

@Injectable({providedIn: 'root'})
export class AccountService {
  constructor(private http: HttpClient) {
  }

  get(): Observable<HttpResponse<EntityType>> {
    return this.http.get<EntityType>(environment.apiUrl + '/account', {observe: 'response'});
  }

  save(account: any): Observable<HttpResponse<any>> {
    return this.http.post(environment.apiUrl + '/account', account, {observe: 'response'});
  }

  update(account: IAccount): Observable<HttpResponse<any>> {
    // const copy = this.convertDateFromClient(account);
    return this.http.put(environment.apiUrl + '/update-my-profile', account, {observe: 'response'})
      .pipe(map((res: EntityResponseType) => this.convertDateFromServer(res)));
  }

  requestRegister(account: any): Observable<HttpResponse<any>> {
    return this.http.post(environment.apiUrl + '/register', account, {observe: 'response'});
  }

  changePassword(passwordChange: IPasswordChange) {
    return this.http.post(environment.apiUrl + '/account/change-password', passwordChange, {observe: 'response'});
  }

  // upload(formData): Observable<any> {
  //   const httpOptions = {
  //     headers: new HttpHeaders({
  //       'Content-Type': 'multipart/form-data'
  //     }),
  //     responseType: 'text'
  //   };
  //   return this.http.post<any>(environment.apiUrl + '/user/upload-avatar/3', formData, httpOptions).subscribe(
  //     (res) => console.log(res),
  //     (err) => console.log(err)
  //   );
  // }

  upload(formData, id_account): Observable<any> {
    return this.http.post(
      `${environment.apiUrl}/users/upload-avatar/${id_account}`,
      formData, { responseType : 'text'}
    );
  }

  authorities(): Observable<string[]> {
    return this.http.get<string[]>(environment.apiUrl + '/users/authorities');
  }

  authorization(roleId): Observable<HttpResponse<EntityAuthorityResponseType>> {
    const url = 'api/rules/permissions';
    // TODO: default 1000 rule
    return this.http.get<EntityAuthorityResponseType>(environment.apiUrl + url + `/${roleId}?size=1000`, {observe: 'response'});
  }

  private convertDateFromClient(account: EntityType): EntityType {
    const copy: EntityType = Object.assign({}, account, {
      birthday: account.birthday != null && account.birthday.isValid() ? account.birthday.format(DATE_TIME_FORMAT) : null
    });
    return copy;
  }

  private convertDateFromServer(res: EntityResponseType): EntityResponseType {
    res.body.birthday = res.body.birthday != null ? moment(res.body.birthday) : null;
    return res;
  }
}
