import { Principal } from './../../core/auth/principal.service';
import { Search } from './../../shared/models/search.model';
import { LocalStorageService } from 'ngx-webstorage';
import { SocketIoService } from './../../core/tracker/socket-io.service';
import { INotificationCollection, NotificationCollection, INotification } from './notification.model';
import { map } from 'rxjs/operators';
import { NotificationService } from './notification.service';
import { Component, OnInit, OnDestroy, HostListener, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Account } from '../../core/account/account.model';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss'],
  // tslint:disable-next-line: use-host-property-decorator
  host: {
    '(document:click)': 'onClick($event)',
  },
})
export class NotificationComponent implements OnInit, OnDestroy {
  collection: INotificationCollection;
  socketClient: any;
  notificationInterval: any;
  searchRequest: Search;
  lastItem: INotification;
  toggle = false;
  constructor(
    private service: NotificationService,
    private socketIoService: SocketIoService,
    private router: Router,
    private localStorageService: LocalStorageService,
    private elementRef: ElementRef,
    public principal: Principal,
  ) {
    this.socketClient = this.socketIoService.client;
    this.collection = new NotificationCollection();
    this.searchRequest = {
      size: 20,
      sort: 'sendDate,DESC',
      page: 0,
    };
  }

  ngOnInit() {
    if (this.principal.isAuthenticated()) {
      if (!this.service.getCollection()) {
        this.fetch();
      } else {
        this.setCollection(this.service.getCollection());
      }
      this.updateNotificationSocketPayload();
    }
  }

  setCollection(collection: INotificationCollection) {
    this.collection = collection;
    this.lastItem = this.collection.notificationDTOs.find(() => true);
  }

  ngOnDestroy() {
    this.clearInterval();
  }

  fetch(callback?: Function) {
    return this.service.collection({...this.searchRequest, exceptLoading: true}).pipe(
      map((res) => res.body),
    ).subscribe(res => {
      if (res) {
        this.service.setCollection(res);
        this.setCollection(res);
        if (callback) {
          callback();
        }
      }
    });
  }

  clearInterval() {
    if (this.notificationInterval) {
      clearInterval(this.notificationInterval);
      console.log('clear noti. interval');
    }
  }

  read(item: INotification) {
    this.service.update({isRead: 1, id: item.id}, {exceptAlert: true, exceptLoading: true}).subscribe(res => {
      if (res.body) {
        this.fetch(() => {
          this.router.navigate(['/standard-board', 'environment'], {queryParams: {}});
        });
      }
    });
  }

   /**
   * Update payload from websocket response
   *
   * @memberof ReportComponent
   */
  updateNotificationSocketPayload() {
    if (this.socketClient) {
      const account: Account = this.localStorageService.retrieve('userdetail');
      if (account) {
        // console.log(account);
        this.notificationInterval = setInterval(() => {
          const req: any = {userId: account.id};
          if (this.lastItem) {
            req.lastId = this.lastItem.id;
          }
          this.socketClient.emit('notification', req, (response: INotificationCollection) => {
            if (response) {
              // console.log(response);
              response.notificationDTOs.forEach(v => this.collection.notificationDTOs.unshift(v));
              this.collection.numberUnreadTotal = response.numberUnreadTotal;
              this.lastItem = this.collection.notificationDTOs.find(() => true);
            }
          });
        }, 7000);
      }
    }
  }

  @HostListener('scroll', ['$event'])
  onScroll(event: any) {
    // visible height + pixel scrolled >= total height
    if (event.target.offsetHeight + event.target.scrollTop >= event.target.scrollHeight) {
      if (this.searchRequest.size > this.collection.notificationDTOs.length) {
        return;
      }
      this.searchRequest.size += 20;
      this.fetch();
    }
  }

  onClick(event) {
    if (!this.elementRef.nativeElement.contains(event.target)) {
      if (this.toggle) {
        this.toggle = false;
      }
    }
  }
}
