import { FilterField, FilterOperator } from '../../models';
import { FilterCriteria } from '../../utils/filter';

export class SelectFilterCriteria extends FilterCriteria {
  loading = false;
  private defaultSize = 20;
  bufferSize = 20;
  constructor(
    public size?: number,
    public key?: FilterField,
    public id?: FilterField
  ) {
    super();
    if (!key) {
      this.key = new FilterField('key', FilterOperator.contains, '');
    }
    if (!id) {
      this.id = new FilterField('id', FilterOperator.equals, null);
    }
    if (!size) {
      this.resetSize();
    }
  }

  resetSize() {
    this.size = this.defaultSize;
  }

  moreSize() {
    this.size += this.bufferSize;
  }
}
