import { NOTIFIER_OPTIONS } from './../../constants/notifier.constant';
import { Component, OnInit, ViewChild } from '@angular/core';
import { JhiEventManager } from 'ng-jhipster';
import { Subscription } from 'rxjs';
import { NotifierService } from 'angular-notifier';
import {LocalStorageService, SessionStorageService} from 'ngx-webstorage';
import { Router } from '@angular/router';
import { NotifierNotificationOptions } from 'angular-notifier/src/models/notifier-notification.model';

@Component({
  selector: 'app-alert-notifier',
  template: `
    <notifier-container></notifier-container>
    <ng-template #notifier let-notification="notification">
      <p class="notifier__notification-message">
        <i *ngIf="notification.type == 'success'" class="far fa-check-circle"></i>
        <i *ngIf="notification.type == 'error'" class="fas fa-ban"></i>
        <i *ngIf="notification.type == 'info'" class="fas fa-info"></i>
        &nbsp;<span [innerHTML]="notification.message | translate: notification.params"></span>
        <ng-container *ngIf="notification.body">
          <br><div [innerHTML]="notification.body"></div>
        </ng-container>
      </p>
      <button class="notifier__notification-button" title="dismiss" type="button">
      <svg class="notifier__notification-button-icon" height="20" viewBox="0 0 24 24" width="20">
        <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"></path>
      </svg>
      </button>
    </ng-template>`,
  styleUrls: []
})
export class AlertNotifierComponent implements OnInit {
  httpListener: Subscription;
  httpErrorListener: Subscription;
  @ViewChild('notifier') notifierTemplate;
  constructor(
    private eventManager: JhiEventManager,
    private notifierService: NotifierService,
    private localStorage: LocalStorageService,
    private sessionStorage: SessionStorageService,
    private router: Router,
  ) {}
  ngOnInit() {
    this.httpListener = this.eventManager.subscribe('app.app-notifier', response => {
      const content = response.content;
      const config = response.config;
      const message = content.message;
      const body = content.body;
      const identity = content.params || '';
      if (config) {
        if (config.behaviour) {
          if (typeof config.behaviour.autoHide !== 'undefined') {
            this.notifierService.getConfig().behaviour.autoHide = config.behaviour.autoHide;
          }
        }
      } else {
        this.notifierService.getConfig().behaviour.autoHide = NOTIFIER_OPTIONS.behaviour.autoHide;
      }
      this.notifierService.show({
        // TODO: clear prefix header param.
        message: typeof message === 'string' ? message.replace('angular2WebApp.', '') : '',
        type: content.type,
        template: this.notifierTemplate,
        params: {param: identity},
        body,
      } as NotifierNotificationOptions);
    });

    this.httpErrorListener = this.eventManager.subscribe('app.httpError', response => {
      const httpErrorResponse = response.content;
      switch (httpErrorResponse.status) {
        // connection refused, server not reachable
        case 0:
          this.addErrorAlert('Server not reachable', 'error.server.not.reachable');
          break;
        case 400:
          this.addErrorAlert400(httpErrorResponse);
          break;
        case 404:
          this.addErrorAlert('Not found', 'error.url.not.found');
          break;
        case 401:
          this.addErrorAlert('login.messages.error.authentication');
          break;
        case 403:
          // check auth
          if (this.localStorage.retrieve('authenticationToken') !== null || this.sessionStorage.retrieve('authenticationToken') !== null) {
            this.addErrorAlert('login.messages.error.forbidden');
          }
          break;
        case 502:
          return this.router.navigate(['accessdenied']).then(() => {});
          break;
        default:
          if (httpErrorResponse.error !== undefined && httpErrorResponse.error !== null) {
            if (httpErrorResponse.error !== '' && httpErrorResponse.error.message) {
              this.addErrorAlert(httpErrorResponse.error.message);
            } else {
              this.addErrorAlert(httpErrorResponse.error);
            }
          }
          break;
      }
    });
  }
  addErrorAlert(message, key?, data?) {
    this.notifierService.show({
      message: message, type: 'error',
      template: this.notifierTemplate
    });
  }

  addErrorAlert400(httpErrorResponse) {
    const arr = httpErrorResponse.headers.keys();
    let errorHeader = null;
    let entityKey = null;
    arr.forEach(entry => {
      if (entry.toLowerCase().endsWith('app-error')) {
        errorHeader = httpErrorResponse.headers.get(entry);
        errorHeader = decodeURIComponent(escape(errorHeader));
      } else if (entry.toLowerCase().endsWith('app-params')) {
        entityKey = httpErrorResponse.headers.get(entry);
      }
    });
    if (errorHeader) {
      const entityName = entityKey;
      this.addErrorAlert(errorHeader, errorHeader, {entityName});
    } else if (httpErrorResponse.error !== '' && httpErrorResponse.error.fieldErrors) {
      const fieldErrors = httpErrorResponse.error.fieldErrors;
      for (let i = 0; i < fieldErrors.length; i++) {
        const fieldError = fieldErrors[i];
        if (['Min', 'Max', 'DecimalMin', 'DecimalMax'].includes(fieldError.message)) {
          fieldError.message = 'Size';
        }
        // convert 'something[14].other[4].id' to 'something[].other[].id' so translations can be written to it
        const convertedField = fieldError.field.replace(/\[\d*\]/g, '[]');
        const fieldName = convertedField.charAt(0).toUpperCase() + convertedField.slice(1);
        this.addErrorAlert('Error on field "' + fieldName + '"', 'error.' + fieldError.message, {fieldName});
      }
    } else if (httpErrorResponse.error !== '' && httpErrorResponse.error.violations) {
      this.addValidationErrorAlert(httpErrorResponse);
    } else if (httpErrorResponse.error !== '' && httpErrorResponse.error.message) {
      this.addErrorAlert(
        `error.${httpErrorResponse.error.message}`,
        httpErrorResponse.error.message,
        httpErrorResponse.error.params
      );
    } else {
      this.addErrorAlert(httpErrorResponse.error.title);
    }
  }

  addValidationErrorAlert(httpErrorResponse) {
    const message = httpErrorResponse.error.message;
    // Violation response
    const data = httpErrorResponse.error.violations;
    switch (message) {
      case 'error.validation':
        let errors = '<ol>';
        for (const item of data) {
          errors += `<li>${item.field} ${item.message}</li>`;
        }
        errors += '</ol>';
        this.addErrorAlert(errors);
        break;
    }
  }
}
