import { SocketIoService } from './../tracker/socket-io.service';
import {Injectable} from '@angular/core';
import {Observable, Subject} from 'rxjs';
import {AccountService} from '../account/account.service';
import {PermissionService} from '../account/permisions.service';
import {LocalStorageService} from 'ngx-webstorage';
import { FcmService } from '../fire/fcm.service';

@Injectable({providedIn: 'root'})
export class Principal {
  private userIdentity: any;
  private authenticated = false;
  private authenticationState = new Subject<any>();
  private permissions: string[];
  private roleId: string;

  constructor(
    private account: AccountService,
    private permissionService: PermissionService,
    private $localStorage: LocalStorageService,
    private socketIoService: SocketIoService,
    private fcmService: FcmService,
  ) {
    this.permissions = [];
  }

  hasAnyAuthority(authorities: string[]): Promise<boolean> {
    return Promise.resolve(this.hasAnyAuthorityDirect(authorities));
  }

  hasAnyAuthorityDirect(authorities: string[]): boolean {
    if (!this.authenticated || !this.userIdentity || !this.roleId) {
      return false;
    }

    for (let i = 0; i < authorities.length; i++) {
      if (this.permissions.includes(authorities[i])) {
        return true;
      }
    }

    return false;
  }

  identity(force?: boolean): Promise<any> {
    if (force === true) {
      this.userIdentity = undefined;
    }

    // check and see if we have retrieved the userIdentity data from the server.
    // if we have, reuse it by immediately resolving
    if (this.userIdentity) {
      return Promise.resolve(this.userIdentity);
    }

    // retrieve the userIdentity data from the server, update the identity object, and then resolve.
    return this.account
      .get()
      .toPromise()
      .then(async response => {
        const account = response.body;
        if (account) {
          this.userIdentity = account;
          this.socketIoService.connect();
        } else {
          this.userIdentity = null;
        }
        await this.permissionService.store(account.roleId);
        await this.fcmService.requestPermission();
        this.setAuthorization(this.permissionService.permissions, account.roleId);
        this.setUserIdentity(this.userIdentity);
        return this.userIdentity;
      })
      .catch(err => {
        if (this.socketIoService.client) {
          this.socketIoService.disconnect();
        }
        this.setUserIdentity(null);
        return null;
      });
  }

  isAuthenticated(): boolean {
    return this.authenticated;
  }

  isIdentityResolved(): boolean {
    return this.userIdentity !== undefined;
  }

  getAuthenticationState(): Observable<any> {
    return this.authenticationState.asObservable();
  }

  getImageUrl(): string {
    return this.isIdentityResolved() ? this.userIdentity.imageUrl : null;
  }

  setAuthorization(rules: string[], roleId: string) {
    this.permissions = rules;
    this.roleId = roleId;
  }

  setUserIdentity(user: any): void {
    this.userIdentity = user;
    this.authenticated = this.userIdentity !== null;
    this.setUserDetailLocalStorage();
    this.authenticationState.next(this.userIdentity);
  }
  setUserDetailLocalStorage() {
    if (this.userIdentity) {
      const userDetail = {
        'firstName': this.userIdentity.fullName,
        'avatar': this.userIdentity.avatar,
        'imageUrl': this.userIdentity.imageUrl,
        'roleName': this.userIdentity.roleName,
        'customerId': this.userIdentity.customerId,
        'roleId': this.userIdentity.roleId,
        'id': this.userIdentity.id,
      };
      this.$localStorage.store('userDetail', userDetail);
    }
  }
}
