import { Injectable } from '@angular/core';
import { AngularFireMessaging } from '@angular/fire/messaging';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { LocalStorageService, SessionStorageService } from 'ngx-webstorage';
import { Observable } from 'rxjs';
import { environment } from '../../../environments';

type EntityResponseType = HttpResponse<any>;

/**
 * Firebase Cloud Messaging service
 *
 * @export
 * @class FcmService
 */
@Injectable({
  providedIn: 'root'
})
export class FcmService {
  private _param = 'fcmToken';
  constructor(
    private afMessaging: AngularFireMessaging,
    private http: HttpClient,
    private $localStorage: LocalStorageService,
    private $sessionStorage: SessionStorageService,
  ) { }

  getToken() {
    return this.$localStorage.retrieve(this._param) || this.$sessionStorage.retrieve(this._param);
  }

  private updateFcmToken(token) {
     return this.http.put(environment.apiUrl + '/update-fcmToken', token, {
       observe: 'response', params: {exceptAlert: true} as any,
      })
     .pipe(
       map((res: EntityResponseType) => this.convertFromServer(res))
      );
  }

  private convertFromServer(res: EntityResponseType): EntityResponseType {
    return res;
  }

  async requestPermission() {
    return this.afMessaging.requestPermission.subscribe((res) => this.requestToken(res));
  }

  async requestToken(res?: any) {
    // console.log(res);
    const tokenStore = this.$localStorage.retrieve(this._param);
    if (tokenStore) {
      return tokenStore;
    }
    return this.afMessaging.requestToken
    .subscribe(
      (token) => {
        console.log('Permission granted! Save to the server!', token);
        return this.updateFcmToken(token).subscribe(() => {
          // console.log(res);
          this.storeToken(token);
          return token;
        });
      },
      (error) => { console.warn(error); },
    );
  }

  private storeToken(token) {
    this.$localStorage.store(this._param, token);
  }

  clear(): Observable<any> {
    return new Observable(observer => {
      this.$localStorage.clear(this._param);
      observer.complete();
    });
  }
}
