import {
  AfterViewChecked, Component, Input, OnChanges, OnInit, SimpleChanges,
  ViewChild
} from '@angular/core';
import {Color, Label} from 'ng2-charts';
import {ChartOptions, ChartType} from 'chart.js';
import {HttpResponse} from '@angular/common/http';
import {IModelChart, ModelChart} from './chart.model';

@Component({
  selector: 'app-chart',
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.scss']
})
export class ChartComponent implements OnInit, OnChanges, AfterViewChecked {
  public barChartOptions: ChartOptions = {
    responsive: true,
    // We use these empty structures as placeholders for dynamic theming.
    scales: {xAxes: [{}], yAxes: [{}]},
  };
  public barChartLabels: Label[] = [];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  @Input() barChartData = [];
  @Input() chartLabels = [];

  @Input() chartColors: Color[] = [
    { // green
      backgroundColor: 'rgba(63,191,63,0.3)',
      borderColor: 'rgba(51,153,51,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    },
  ];

  // events
  public chartClicked({event, active}: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }

  public chartHovered({event, active}: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }

  public randomize(): void {
    this.barChartType = this.barChartType === 'bar' ? 'line' : 'bar';
  }

  constructor() {
  }

  ngOnInit() {
    this.barChartOptions = Object.assign({
      responsive: true,
      title: {
        display: false,
        text: 'Chart Title'
      },
      legend: {
        display: true,
        labels: {
          // This more specific font property overrides the global property
          // fontColor: 'red'
        }
      },
      scales: {
        yAxes: [{
          ticks: {
            // Include a dollar sign in the ticks
            // callback: function(value, index, values) {
            //   return '$' + value;
            // }
          }
        }],
        xAxes: [{
          ticks: {
            // Include a dollar sign in the ticks
            // callback: function(value, index, values) {
            //   return '$' + value;
            // },
            // fontColor: 'red',
            fontSize: 10,
            maxTicksLimit: 100,
            min: 0,
            max: 200,
            display: true,
            autoSkip: false
          }
        }],
      },
      plugins: {
        datalabels: {
          anchor: 'end',
          align: 'end',
          font: {
            size: 10,
          },
          padding: {
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
          },
          opacity: 0.7
        }
      },
      hover: {
        animationDuration: 0,
      },
      animation: {
        duration: 0,
      }
    }, this.barChartOptions);
    this.initChartData();
  }

  initChartData() {
    // this.homeService.queryDataChartById(this.homeService.route.shrimpLitterId).subscribe(
    //   (res: HttpResponse<IModelChart[]>) => this.onSuccess(res.body)
    // );
  }

  private onSuccess(data_res: any) {
    if (data_res.listShrimpWeightOfShrimpLittersDTOs != null) {
      const temp = [];

      data_res.listShrimpWeightOfShrimpLittersDTOs.forEach(e => {
        this.chartLabels.push(e.numberDay + '/' + e.numberMonth + '/' + e.numberYear);
        temp.push(e.shrimpWeight);
      });
      this.barChartLabels = this.chartLabels;
      const data_temp = {data: temp, label: data_res.shrimpPondName};
      this.barChartData = [data_temp];
    }
  }

  ngOnChanges(changes: SimpleChanges): void {

  }
  ngAfterViewChecked(): void {
  }

}
