
import { GuestLayoutComponent } from './layouts/guest-layout/guest-layout.component';
import { UserRouteAccessService } from './core/auth/user-route-access-service';
import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { matTemplatesRoutes } from './mat-templates/mat-templates.route';
import { errorRoutes } from './layouts';
import { UserRouteAccessDashboardService } from './core/auth/user-route-access-dashboard-service';
import { environment } from '../environments/environment';

const commonRoutes = [
  ...errorRoutes,
  ...matTemplatesRoutes,
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      [
        ...commonRoutes,
        {
          path: '',
          redirectTo: 'dashboard',
          pathMatch: 'full'
        },
        {
          path: '',
          component: AdminLayoutComponent,
          canActivate: [UserRouteAccessService],
          loadChildren: './admin/admin.module#AdminModule',
        },
        {
          path: '',
          component: GuestLayoutComponent,
          canActivate: [UserRouteAccessDashboardService],
          loadChildren: './guest/guest.module#GuestModule',
        },
      ],
      { enableTracing: environment.debug }
    )],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule { }
